export default {
    name: 'LanguageChange',
    created: function () {
        this.init();
    },
    data: function () {
        return {
            translations: jsdi.services.languageService.translations
        };
    },
    methods: {
        init: function () {
            console.log("LanguageChange init");
        },
        changeTo: function (language) {
            jsdi.services.languageService.changeTo(language);
        }
    }
}



